'use client'

import { MetricsCards } from '@/components/dashboard/metrics-cards'
import { ActiveTasksList } from '@/components/dashboard/active-tasks-list'
import { useWebSocket } from '@/lib/hooks/use-websocket'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { WorkflowVisualization } from '@/components/workflow/workflow-visualization'

export function MissionControlDashboard() {
  // Enable WebSocket for real-time updates
  useWebSocket(true)

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Mission Control</h1>
        <p className="text-muted-foreground">
          Monitor and manage your AI agent workflows
        </p>
      </div>

      <MetricsCards />

      <div className="grid gap-6 md:grid-cols-2">
        <ActiveTasksList />
        
        {/* Workflow Visualization */}
        <Card>
          <CardHeader>
            <CardTitle>Workflow Visualization</CardTitle>
            <CardDescription>
              Visual representation of active workflows and agent interactions
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="rounded-lg border border-dashed p-12 text-center bg-muted/30">
              <Workflow className="h-12 w-12 mx-auto mb-4 text-muted-foreground opacity-50" />
              <p className="text-sm font-medium text-muted-foreground mb-1">
                Workflow visualization
              </p>
              <p className="text-xs text-muted-foreground">
                Coming soon - Interactive graph view of tasks, plans, and agent workflows
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
